<template>
  <div class="templateWindow">
    <div class="windowContent">
      <!--    -->
      <div style="margin-top: 9px">
        <el-button type="primary" icon="el-icon-circle-plus-outline"
                   @click="clientDialogFormVisible = true;">
        </el-button>
        <!--    -->
        <el-select v-model="SelectValue"
                   @change="clientFilter"
                   placeholder=""
                   style="margin-left: 15px">
          <el-option-group
            v-for="group in options"
            :key="group.label"
            :label="group.label">
            <el-option
              v-for="item in group.options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-option-group>
        </el-select>
      </div>
      <!--    -->
      <el-table
        :data="clientTableData"
        border
        style="width: 100%;margin-top: 7px">
        <el-table-column
          fixed="left"
          prop="name"
          label=""
          width="140">
        </el-table-column>
        <el-table-column
          prop="type"
          label=""
          :formatter="clientType"
          width="100">
        </el-table-column>
        <el-table-column
          prop="statue"
          label=""
          :formatter="clientStatue"
          width="100">
        </el-table-column>
        <el-table-column
          prop="manager"
          label=""
          width="100">
        </el-table-column>
        <el-table-column
          prop="require"
          label=""
          width="200">
        </el-table-column>
        <el-table-column
          prop="phone"
          label=""
          width="120">
        </el-table-column>
        <el-table-column
          prop="datetime"
          label=""
          width="160">
        </el-table-column>
        <el-table-column
          prop="complete"
          label=""
          :formatter="clientComplete"
          width="100">
        </el-table-column>
        <el-table-column
          prop="endedittime"
          label=""
          width="160">
        </el-table-column>
        <el-table-column
          prop="call"
          label=""
          width="100">
        </el-table-column>
        <el-table-column
          prop="address"
          label=""
          :formatter="clientAddress"
          width="160">
        </el-table-column>
        <el-table-column
          prop="industry"
          label=""
          :formatter="clientIndustry"
          width="100">
        </el-table-column>
        <el-table-column
          fixed="right"
          label=""
          width="130">
          <template slot-scope="scope">
            <el-button @click="clientSelectClick(scope.row)" type="text" size="small"></el-button>
            <el-button type="text" size="small" @click="clientEditInfo(scope.row)"></el-button>
            <el-button type="text" size="small" @click="clientDeleteInfo(scope.row)" style="color: red!important;">
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  dialog  -->
      <el-dialog title="" :visible.sync="clientDialogFormVisible">
        <el-form :model="clientForm" :inline=true :rules="clientRules">
          <el-form-item label="" :label-width="clientFormLabelWidth" prop="name">
            <el-input v-model="clientForm.name" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="clientFormLabelWidth" prop="formType">
            <el-select v-model="clientForm.formType" placeholder="">
              <el-option label="" value="1"></el-option>
              <el-option label="" value="0"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" :label-width="clientFormLabelWidth" prop="statue">
            <el-select v-model="clientForm.statue" placeholder="">
              <el-option label="" value="1"></el-option>
              <el-option label="" value="2"></el-option>
              <el-option label="" value="3"></el-option>
              <el-option label="" value="4"></el-option>
              <el-option label="" value="5"></el-option>
              <el-option label="" value="0"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" :label-width="clientFormLabelWidth" prop="manager">
            <el-input v-model="clientForm.manager" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="clientFormLabelWidth" prop="require">
            <el-input v-model="clientForm.require" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="clientFormLabelWidth" prop="phone">
            <el-input v-model="clientForm.phone" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="clientFormLabelWidth" prop="callName">
            <el-input v-model="clientForm.callName" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="clientFormLabelWidth" prop="address">
            <el-select v-model="clientForm.address" placeholder="">
              <el-option label="" value="1"></el-option>
              <el-option label="" value="2"></el-option>
              <el-option label="" value="3"></el-option>
              <el-option label="" value="4"></el-option>
              <el-option label="" value="5"></el-option>
              <el-option label="" value="6"></el-option>
              <el-option label="" value="7"></el-option>
              <el-option label="" value="0"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" :label-width="clientFormLabelWidth" prop="industry">
            <el-select v-model="clientForm.industry" placeholder="">
              <el-option label="IT" value="1"></el-option>
              <el-option label="" value="2"></el-option>
              <el-option label="" value="3"></el-option>
              <el-option label="" value="4"></el-option>
              <el-option label="" value="5"></el-option>
              <el-option label="" value="6"></el-option>
              <el-option label="" value="7"></el-option>
              <el-option label="" value="0"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="clientDialogFormVisible = false"> </el-button>
          <el-button type="primary" @click="ClientDialogFormVisible = false;clientAddInfo()"> </el-button>
        </div>
      </el-dialog>
      <!--  dialog  -->
      <el-dialog title="" :visible.sync="clientShowDialogTableVisible">
        <el-table :data="clientShowTableInfoData">
          <el-table-column property="name" label="" width="190"></el-table-column>
          <el-table-column property="type" label="" width="150" :formatter="clientType"></el-table-column>
          <el-table-column property="statue" label="" width="190" :formatter="clientStatue"></el-table-column>
          <el-table-column property="manager" label="" width="120"></el-table-column>
        </el-table>
        <el-table :data="clientShowTableInfoData">
          <el-table-column property="require" label="" width="190"></el-table-column>
          <el-table-column property="phone" label="" width="150"></el-table-column>
          <el-table-column property="datetime" label="" width="190"></el-table-column>
          <el-table-column property="complete" label="" width="120" :formatter="clientComplete"></el-table-column>
        </el-table>
        <el-table :data="clientShowTableInfoData">
          <el-table-column property="endedittime" label="" width="190"></el-table-column>
          <el-table-column property="call" label="" width="150"></el-table-column>
          <el-table-column property="address" label="" width="190" :formatter="clientAddress"></el-table-column>
          <el-table-column property="industry" label="" width="120" :formatter="clientIndustry"></el-table-column>
        </el-table>
      </el-dialog>
      <!--  dialog    -->
      <el-dialog title="" :visible.sync="clientEditDialogFormVisible">
        <el-form :model="clientEditFormData" :inline=true :rules="clientRules">
          <el-form-item label="" :label-width="clientFormLabelWidth" prop="name">
            <el-input v-model="clientEditFormData.name" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="clientFormLabelWidth" prop="formType">
            <el-select v-model="clientEditFormData.type" placeholder="">
              <el-option label="" value="0"></el-option>
              <el-option label="" value="1"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" :label-width="clientFormLabelWidth" prop="statue">
            <el-select v-model="clientEditFormData.statue" placeholder="">
              <el-option label="" value="1"></el-option>
              <el-option label="" value="2"></el-option>
              <el-option label="" value="3"></el-option>
              <el-option label="" value="4"></el-option>
              <el-option label="" value="5"></el-option>
              <el-option label="" value="0"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" :label-width="clientFormLabelWidth" prop="manager">
            <el-input v-model="clientEditFormData.manager" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="clientFormLabelWidth" prop="require">
            <el-input type="textarea" :rows="2" placeholder=""
                      autocomplete="off"
                      v-model="clientEditFormData.require">
            </el-input>
          </el-form-item>
          <el-form-item label="" :label-width="clientFormLabelWidth" prop="phone">
            <el-input v-model="clientEditFormData.phone" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="clientFormLabelWidth" prop="formType">
            <el-select v-model="clientEditFormData.complete" placeholder="">
              <el-option label="" value="0"></el-option>
              <el-option label="" value="1"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" :label-width="clientFormLabelWidth" prop="callName">
            <el-input v-model="clientEditFormData.call" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="clientFormLabelWidth" prop="address">
            <el-select v-model="clientEditFormData.address" placeholder="">
              <el-option label="" value="1"></el-option>
              <el-option label="" value="2"></el-option>
              <el-option label="" value="3"></el-option>
              <el-option label="" value="4"></el-option>
              <el-option label="" value="5"></el-option>
              <el-option label="" value="6"></el-option>
              <el-option label="" value="7"></el-option>
              <el-option label="" value="0"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" :label-width="clientFormLabelWidth" prop="industry">
            <el-select v-model="clientEditFormData.industry" placeholder="">
              <el-option label="IT" value="1"></el-option>
              <el-option label="" value="2"></el-option>
              <el-option label="" value="3"></el-option>
              <el-option label="" value="4"></el-option>
              <el-option label="" value="5"></el-option>
              <el-option label="" value="6"></el-option>
              <el-option label="" value="7"></el-option>
              <el-option label="" value="0"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="clientEditDialogFormVisible = false;"> </el-button>
          <el-button type="primary" @click="clientEditDialogFormVisible = false;clientEditDialogTrue()"> </el-button>
        </div>
      </el-dialog>
      <!--     -->
      <div style="position: fixed;bottom: 35px;">
        <el-pagination
          :page-size="clientPageSize"
          :total="clientTotalPage"
          :current-page.sync="clientCurrentPage"
          background
          @current-change="clientCurrentChange"
          layout="prev, pager, next">
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "clientInfo",
  data() {
    return {
      // 
      options: [
        {
          label: '',
          options: [{
            value: '1all',
            label: ''
          }]
        },
        {
          label: '',
          options: [{
            value: '1type',
            label: ''
          }, {
            value: '0type',
            label: ''
          }]
        },
        {
          label: '',
          options: [{
            value: '1complete',
            label: ''
          }, {
            value: '0complete',
            label: ''
          }]
        },
        {
          label: '',
          options: [{
            value: '1statue',
            label: ''
          }, {
            value: '2statue',
            label: ''
          }, {
            value: '3statue',
            label: ''
          }, {
            value: '4statue',
            label: ''
          }, {
            value: '5statue',
            label: ''
          }, {
            value: '0statue',
            label: ''
          }]
        },
        {
          label: '',
          options: [{
            value: '1address',
            label: ''
          }, {
            value: '2address',
            label: ''
          }, {
            value: '3address',
            label: ''
          }, {
            value: '4address',
            label: ''
          }, {
            value: '5address',
            label: ''
          }, {
            value: '6address',
            label: ''
          }, {
            value: '7address',
            label: ''
          }, {
            value: '8address',
            label: ''
          }]
        },
        {
          label: '',
          options: [{
            value: '1industry',
            label: 'IT'
          }, {
            value: '2industry',
            label: ''
          }, {
            value: '3industry',
            label: ''
          }, {
            value: '4industry',
            label: ''
          }, {
            value: '5industry',
            label: ''
          }, {
            value: '6industry',
            label: ''
          }, {
            value: '7industry',
            label: ''
          }, {
            value: '8industry',
            label: ''
          }]
        }],
      // 
      isFilter: false,
      // 
      filterTableData: [],
      // value
      SelectValue: '',
      // table
      clientTableData: [],
      // 
      clientGetServerData: [],
      // 
      clientCurrentPage: 1,
      // 
      clientTotalPage: 56,
      // 
      clientPageSize: 7,
      // 
      clientDialogTableVisible: false,
      clientDialogFormVisible: false,
      // 
      clientShowDialogTableVisible: false,
      // 
      clientShowTableInfoData: [],
      // 
      clientForm: {
        name: '',
        formType: '',
        statue: '',
        manager: '',
        require: '',
        phone: '',
        address: '',
        callName: '',
        industry: '',
        complete: '',
      },
      clientFormLabelWidth: '120px',
      // 
      clientRules: {
        name: [
          {required: true, message: '', trigger: 'blur'},
          {min: 2, max: 15, message: ' 2  15 ', trigger: 'blur'}
        ],
        formType: [
          {required: true, message: '', trigger: 'change'}
        ],
        statue: [
          {required: true, message: '', trigger: 'change'},
        ],
        manager: [
          {required: true, message: '', trigger: 'blur'}
        ],
        require: [
          {required: true, message: '', trigger: 'blur'},
          {min: 2, max: 20, message: ' 2  20 ', trigger: 'blur'}
        ],
        phone: [
          {required: true, message: '', trigger: 'blur'},
        ],
        complete: [
          {required: true, message: '', trigger: 'change'},
        ],
        address: [
          {required: true, message: '', trigger: 'change'}
        ],
        callName: [
          {required: true, message: '', trigger: 'blur'},
          {min: 3, max: 4, message: ' 3  4 ', trigger: 'blur'}
        ],
        industry: [
          {required: true, message: '', trigger: 'change'}
        ],
      },
      // 
      clientEditDialogFormVisible: false,
      // 
      clientEditFormData: {},
    }
  },
  methods: {
    // select
    clientFilter(value) {
      let num = value[0];
      let group = value.slice(1)
      let tab = this.clientGetServerData
      // 
      this.clientCurrentPage = 1;
      // 
      this.isFilter = true;
      switch (group) {
        case 'type':
          this.filterTableData = tab.filter(tab => tab.type === num);
          break;
        case 'complete':
          this.filterTableData = tab.filter(tab => tab.complete === num);
          break;
        case 'statue':
          this.filterTableData = tab.filter(tab => tab.statue === num);
          break;
        case 'address':
          this.filterTableData = tab.filter(tab => tab.address === num);
          break;
        case 'industry':
          this.filterTableData = tab.filter(tab => tab.industry === num);
          break;
        default:
          this.filterTableData = this.clientGetServerData;
          this.isFilter = false;
          break;
      }
      this.clientTotalPage = this.filterTableData.length
      this.clientTableData = this.filterTableData.slice(0, 7)
    },
    // 
    clientIndustry(row, column, cellValue) {
      if (cellValue === '1') {
        return 'IT'
      } else if (cellValue === '2') {
        return ''
      } else if (cellValue === '3') {
        return ''
      } else if (cellValue === '4') {
        return ''
      } else if (cellValue === '5') {
        return ''
      } else if (cellValue === '6') {
        return ''
      } else if (cellValue === '7') {
        return ''
      } else if (cellValue === '0') {
        return ''
      }
    },
    // 
    clientAddress(row, column, cellValue) {
      if (cellValue === '1') {
        return ''
      } else if (cellValue === '2') {
        return ''
      } else if (cellValue === '3') {
        return ''
      } else if (cellValue === '4') {
        return ''
      } else if (cellValue === '5') {
        return ''
      } else if (cellValue === '6') {
        return ''
      } else if (cellValue === '7') {
        return ''
      } else if (cellValue === '0') {
        return ''
      }
    },
    // 
    clientStatue(row, column, cellValue) {
      if (cellValue === '1') {
        return ''
      } else if (cellValue === '2') {
        return ''
      } else if (cellValue === '3') {
        return ''
      } else if (cellValue === '4') {
        return ''
      } else if (cellValue === '5') {
        return ''
      } else if (cellValue === '0') {
        return ''
      }
    },
    // 
    clientType(row, column, cellValue) {
      if (cellValue === '1') {
        return ''
      } else {
        return ''
      }
    },
    // 
    clientComplete(row, column, cellValue) {
      if (cellValue === '1') {
        return ''
      } else {
        return ''
      }
    },
    // 
    clientCurrentChange(nowPage) {
      console.log('' + nowPage)
      // 
      if (this.isFilter) {
        // 
        this. clientFilterFn(nowPage,this.filterTableData)
      } else {
        // 
        this. clientFilterFn(nowPage,this.clientGetServerData)
      }
    },
    // 
    clientFilterFn(page,dataSource){
      if (page === 1) {
        this.clientTableData = dataSource.slice(0, 7);
      } else {
        let start = (page - 1) * 7
        let end = page * 7
        if (end > dataSource.length) {
          end = dataSource.length;
        }
        this.clientTableData = dataSource.slice(start, end);
      }
    },
    // 
    clientSelectClick(row) {
      console.log(row);
      this.clientShowTableInfoData[0] = row;
      console.log(this.clientShowTableInfoData)
      this.clientShowDialogTableVisible = true
    },
    // 
    clientEditInfo(row) {
      this.clientEditFormData = row;
      console.log(this.clientEditFormData)
      this.clientEditDialogFormVisible = true;
    },
    // 
    clientEditDialogTrue() {
      console.log(this.clientEditFormData)
      axios({
        url: './php/update/update_clientInfo.php',
        method: 'post',
        // 
        data: 'id=' + this.clientEditFormData.id +
          '&name=' + this.clientEditFormData.name +
          '&formType=' + this.clientEditFormData.type +
          '&statue=' + this.clientEditFormData.statue +
          '&manager=' + this.clientEditFormData.manager +
          '&require=' + this.clientEditFormData.require +
          '&phone=' + this.clientEditFormData.phone +
          '&address=' + this.clientEditFormData.address +
          '&callName=' + this.clientEditFormData.call +
          '&complete=' + this.clientEditFormData.complete +
          '&industry=' + this.clientEditFormData.industry,
        // 
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then((res) => {
        if (res.data === 2) {
          this.$message.success('')
        }
      }).catch(function (error) { // 
        console.log(error);
      });
      this.clientGetUserData()

    },
    // 
    clientDeleteInfo(row) {
      axios
        .get('./php/delete/delete_client.php?id=' + row.id)
        .then((res) => {
          if (res.data === 11) {
            this.$message.success('')
            this.clientGetUserData()
            this.clientCurrentPage = 1;
          } else {
            this.$message.error('')
          }
        })
        .catch(function (error) { // 
          console.log(error);
        });
    },
    // 
    clientGetUserData() {
      axios
        .get('./php/select/select_clientInfo.php')
        .then((res) => {
          this.clientGetServerData = res.data;
          this.clientTableData = this.clientGetServerData.slice(0, 7)
          this.clientTotalPage = this.clientGetServerData.length
        })
        .catch(function (error) { // 
          console.log(error);
        });
    },
    // 
    clientAddInfo() {
      this.clientDialogFormVisible = false;
      axios({
        url: './php/insert/insert_ClientInfo.php',
        method: 'post',
        // 
        data: 'name=' + this.clientForm.name +
          '&formType=' + this.clientForm.formType +
          '&statue=' + this.clientForm.statue +
          '&manager=' + this.clientForm.manager +
          '&require=' + this.clientForm.require +
          '&phone=' + this.clientForm.phone +
          '&address=' + this.clientForm.address +
          '&callName=' + this.clientForm.callName +
          '&industry=' + this.clientForm.industry,
        // 
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then((res) => {
        if (res.data === 2) {
          this.$message.success('')
          this.clientGetUserData()
        } else {
          this.$message.error('')
        }
      }).catch(function (error) { // 
        console.log(error);
      });
    }
  },
  created() {
    this.clientGetUserData()
  }
}
</script>

<style scoped>
.templateWindow {
  height: calc(100% - 40px);
  width: 100%;
  background-color: #D3DCE6;
  position: absolute;
  left: 0;
  top: 40px
}

.windowContent {
  position: absolute;
  left: 10px;
  top: 0;
  width: calc(100% - 10px);
  height: calc(100% - 10px);
}
</style>



