<template>
  <div class="templateWindow">
    <div style="position: absolute;left: 10px;top: 10px;width: 350px">
      <el-form :model="updatePwdRuleForm"
               :rules="updatePwdRules"
               ref="ruleForm"
               :label-position="updatePwdLabelPosition"
               class="demo-ruleForm">
        <el-form-item label="" prop="oldPwd">
          <el-input v-model="updatePwdRuleForm.oldPwd" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="newPwd">
          <el-input v-model="updatePwdRuleForm.newPwd" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="isNewPwd">
          <el-input v-model="updatePwdRuleForm.isNewPwd" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button @click="resetForm('ruleForm')"></el-button>
          <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "updatePwd",
  data() {
    return {
      /*
-----------------------------------------------------------
               
 */
      // 
      updatePwdRuleForm: {
        oldPwd: '',
        newPwd: '',
        isNewPwd: '',
      },
      // 
      updatePwdRules: {
        oldPwd: [
          {required: true, message: '', trigger: 'blur'},
          {min: 6, max: 14, message: ' 6  14 ', trigger: 'blur'}
        ],
        newPwd: [
          {required: true, message: '', trigger: 'blur'},
          {min: 6, max: 14, message: ' 6  14 ', trigger: 'blur'}
        ],
        isNewPwd: [
          {required: true, message: '', trigger: 'blur'},
          {min: 6, max: 14, message: ' 6  14 ', trigger: 'blur'}
        ],
      },
      updatePwdLabelPosition: 'left',
    }
  },
  methods: {
    /*
-----------------------------------------------------------
            
*/
    // 
    submitForm(formName) {
      let newPwd = this.updatePwdRuleForm.newPwd;
      let oldPwd = this.updatePwdRuleForm.oldPwd;
      let isNewPwd = this.updatePwdRuleForm.isNewPwd;
      if (newPwd.length === 0) {
        this.$message.error('')
      } else if (oldPwd.length === 0) {
        this.$message.error('')
      } else if (isNewPwd.length === 0) {
        this.$message.error('')
      } else if (newPwd === oldPwd) {
        this.$message.error('')
      } else {
        if (newPwd === isNewPwd) {
          this.$refs[formName].validate((valid) => {
            if (valid) {
              axios({
                url: './php/update/update_Pwd.php',
                method: 'post',
                // 
                data: 'oldPwd=' + oldPwd +
                  '&newPwd=' + newPwd +
                  '&isNewPwd=' + isNewPwd,
                // 
                headers: {
                  'Content-Type': 'application/x-www-form-urlencoded'
                }
              }).then((res) => {
                if (res.data === 7) {
                  this.$message.success('');
                  setTimeout(()=>{
                    window.location.reload()
                  },1000)
                } else if (res.data === 8) {
                  this.$message.warning(',');
                  this.updatePwdRuleForm.isNewPwd = ''
                  this.updatePwdRuleForm.newPwd = ''
                  this.updatePwdRuleForm.oldPwd = ''
                }
              }).catch(function (error) { // 
                console.log(error);
              });
            } else {
              this.$message.error('')
              return false;
            }
          });
        } else {
          this.$message.error('')
        }
      }
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
      this.updatePwdRuleForm.isNewPwd = ''
      this.updatePwdRuleForm.newPwd = ''
      this.updatePwdRuleForm.oldPwd = ''
    },
  }
}
</script>

<style scoped>
.templateWindow {
  height: calc(100% - 40px);
  width: 100%;
  background-color: #D3DCE6;
  position: absolute;
  left: 0;
  top: 40px
}

.windowContent {
  position: absolute;
  left: 10px;
  top: 10px;
  width: calc(100% - 10px);
  height: calc(100% - 10px);
}
</style>



