<template>
  <div>
    <div style="margin-bottom: 10px">
      <el-switch
        v-model="isTheme"
        @change="mapDark"
        active-text=""
        inactive-text=""
        active-color="#13ce66"
        inactive-color="#ff4949">
      </el-switch>
    </div>
    <div ref="main" id="main" style="width: 500px;height:500px;"></div>
  </div>
</template>

<script>
import * as echarts from 'echarts';
import axios from "axios";

let myChart;
export default {
  name: "mapClientIndustry",
  data(){
    return {
      addressValue:[],
      isTheme:false,
      theme:'',
    }
  },
  methods: {
    mapDark(sw){
      console.log(sw)
      this.isTheme = sw;
      this.isTheme ? this.theme = 'dark' : this.theme = ''
      this.createCountMap()
    },
    createCountMap() {
      // s
      if ( myChart != null && myChart !== "" && myChart !== undefined ) {
        myChart.dispose();
      }

      // domecharts
      myChart = echarts.init(this.$refs.main,this.theme);

      // 
      let option = {
        title: {
          text: '',
          left: 'center'
        },
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b} : {c} ({d}%)'
        },
        legend: {
          left: 'center',
          top: 'bottom',
          data: ['IT', '', '', '', '', '', '', '']
        },
        series: [
          {
            name: '',
            type: 'pie',
            radius: [20, 140],
            center: ['50%', '50%'],
            roseType: 'radius',
            itemStyle: {
              borderRadius: 5
            },
            data: [
              {value: Number(this.addressValue[1]), name: 'IT'},
              {value: Number(this.addressValue[2]), name: ''},
              {value: Number(this.addressValue[3]), name: ''},
              {value: Number(this.addressValue[4]), name: ''},
              {value: Number(this.addressValue[5]), name: ''},
              {value: Number(this.addressValue[6]), name: ''},
              {value: Number(this.addressValue[7]), name: ''},
              {value: Number(this.addressValue[0]), name: ''}
            ]
          }
        ]
      };


      // 
      option && myChart.setOption(option)
    }
  },
  watch:{
    addressValue(){
      this.createCountMap()
    }
  },
  beforeCreate() {
    axios
      .get('./php/select/select_mapData.php?name=industry')
      .then((res) => {
        this.addressValue = res.data
      })
      .catch(function (error) { // 
        console.log(error);
      });
  }
}
</script>

<style scoped>

</style>



