<template>
  <div class="templateWindow">
    <div class="windowContent">
      <!---->
      <div style="margin-top: 9px">
        <el-button type="primary" icon="el-icon-circle-plus-outline"
                   @click="manageDialogAddVisible = true;">
        </el-button>
        <!--    -->
        <el-select v-model="SelectValue"
                   @change="clientFilter"
                   placeholder=""
                   style="margin-left: 15px">
          <el-option-group
            v-for="group in options"
            :key="group.label"
            :label="group.label">
            <el-option
              v-for="item in group.options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-option-group>
        </el-select>
      </div>
      <!---->
      <el-table
        ref="multipleTable"
        :data="manageTableData"
        tooltip-effect="dark"
        style="width: 100%;margin-top: 7px">
        <el-table-column
          fixed
          prop="id"
          label="ID"
          width="70">
        </el-table-column>
        <el-table-column
          fixed
          prop="name"
          label=""
          width="90">
        </el-table-column>
        <el-table-column
          fixed
          prop="username"
          label=""
          width="120">
        </el-table-column>
        <el-table-column
          fixed
          prop="power"
          :formatter="managePowerCN"
          label=""
          width="120">
        </el-table-column>
        <el-table-column
          fixed
          prop="statue"
          :formatter="manageStatueChinese"
          label=""
          width="100">
        </el-table-column>
        <el-table-column
          fixed
          label=""
          width="100">
          <template slot-scope="scope">
            <el-button type="text" size="small"
                       @click="manageDialogEditVisible = true;manageEditInfo(scope.row)">
              
            </el-button>
            <el-button @click="manageHandleClick(scope.row)" type="text" size="small"
                       style="color: red!important;">
              
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <!---->
      <el-dialog title="" :visible.sync="manageDialogEditVisible" width="35%">
        <el-form :model="manageForm" :rules="manageRules">
          <el-form-item label="" :label-width="manageFormLabelWidth" prop="name">
            <el-input v-model="manageForm.name" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="manageFormLabelWidth" prop="username">
            <el-input v-model="manageForm.username" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="manageFormLabelWidth" prop="power">
            <el-select v-model="manageForm.power" placeholder="">
              <el-option label="" value="0"></el-option>
              <el-option label="" value="1"></el-option>
              <el-option label="" value="2"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" :label-width="manageFormLabelWidth">
            <el-select v-model="manageForm.statue" placeholder="" prop="statue">
              <el-option label="" value="0"></el-option>
              <el-option label="" value="1"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="manageDialogEditVisible = false"> </el-button>
          <el-button type="primary"
                     @click="manageDialogEditVisible = false;manageEditInfoTrue()"> 
          </el-button>
        </div>
      </el-dialog>
      <!---->
      <el-dialog title="" :visible.sync="manageDialogAddVisible" width="35%">
        <el-form :model="manageAddUserInfo" :rules="manageRules" ref="addUserInfo"
                 label-width="100px" class="demo-ruleForm">
          <el-form-item label="" prop="name">
            <el-input v-model="manageAddUserInfo.name" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="username">
            <el-input v-model="manageAddUserInfo.username" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="pwd">
            <el-input placeholder="" v-model="manageAddUserInfo.pwd"
                      show-password></el-input>
          </el-form-item>
          <el-form-item label="" prop="power">
            <el-select v-model="manageAddUserInfo.power" placeholder="">
              <el-option label="" value="0"></el-option>
              <el-option label="" value="1"></el-option>
              <el-option label="" value="2"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="statue">
            <el-select v-model="manageAddUserInfo.statue" placeholder="">
              <el-option label="" value="0"></el-option>
              <el-option label="" value="1"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="manageDialogAddVisible = false;manageResetForm('addUserInfo')"></el-button>
          <el-button type="primary" @click="manageSubmitForm('addUserInfo');"> </el-button>
        </div>
      </el-dialog>
      <!---->
      <div style="position: fixed;bottom: 35px;">
        <el-pagination
          small
          :page-size="managePageSize"
          :current-page.sync="manageCurrentPage"
          background
          @current-change="manageCurrentChange"
          layout="prev, pager, next"
          :total="manageTotalPage">
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "managerHtml",
  data() {
    return {
      // 
      options: [
        {
          label: '',
          options: [{
            value: '1all',
            label: ''
          }]
        },
        {
          label: '',
          options: [{
            value: '0power',
            label: ''
          }, {
            value: '1power',
            label: ''
          }, {
            value: '2power',
            label: ''
          }]
        },
        {
          label: '',
          options: [{
            value: '0statue',
            label: ''
          }, {
            value: '1statue',
            label: ''
          }]
        }],
      // 
      isFilter: false,
      // 
      filterTableData: [],
      // value
      SelectValue: '',
      // 
      manageRules: {
        name: [
          {required: true, message: '', trigger: 'blur'},
          {min: 2, max: 4, message: ' 2  4 ', trigger: 'blur'}
        ],
        username: [
          {required: true, message: '', trigger: 'blur'},
          {min: 5, max: 11, message: ' 5  11 ', trigger: 'blur'}
        ],
        pwd: [
          {required: true, message: '', trigger: 'blur'},
          {min: 6, max: 14, message: ' 6  14 ', trigger: 'blur'}
        ],
        power: [
          {required: true, message: '', trigger: 'change'}
        ],
        statue: [
          {required: true, message: '', trigger: 'change'}
        ]
      },
      manageIdx: 0,
      // 
      manageTableData: [],
      manageGetServerData: [],

      manageDialogEditVisible: false,
      manageDialogAddVisible: false,

      // 
      manageForm: {
        id: '',
        name: '',
        username: '',
        power: '',
        statue: '',
        delivery: false,
        type: [],
      },
      manageFormLabelWidth: '100px',
      // 
      manageAddUserInfo: {
        name: '',
        username: '',
        pwd: '',
        power: '',
        statue: '',
      },
      // 
      manageTotalPage: 56,
      // 
      managePageSize: 8,
      // 
      manageCurrentPage: 1,
    }
  },
  methods: {
    // select
    clientFilter(value) {
      let num = value[0];
      let group = value.slice(1)
      this.manageCurrentPage = 1;
      let tab = this.manageGetServerData
      this.isFilter = true;
      switch (group) {
        case 'power':
          this.filterTableData = tab.filter(tab => tab.power === num);
          break;
        case 'statue':
          this.filterTableData = tab.filter(tab => tab.statue === num);
          break;
        default:
          this.filterTableData = this.manageGetServerData;
          this.isFilter = false;
          break;
      }
      this.manageTotalPage = this.filterTableData.length
      this.manageTableData = this.filterTableData.slice(0, 8)
    },
    // 
    manageStatueChinese(row, column, cellValue) {
      if (cellValue === '0') {
        return ''
      } else {
        return ''
      }
    },
    managePowerCN(row, column, cellValue) {
      if (cellValue === '0') {
        return ''
      } else if (cellValue === '1') {
        return ''
      } else {
        return ''
      }
    },
    // 
    manageHandleClick(row) {
      // 
      axios
        .get('./php/delete/delete_manager.php?id=' + row.id)
        .then((res) => {
          if (res.data === 11) {
            this.manageGetUserData()
            this.$message.success('');
            this.manageCurrentPage = 1;
          } else {
            this.$message.error('');
          }

        })
        .catch(function (error) { // 
          console.log(error);
        });
    },
    // 
    manageEditInfo(rows) {
      this.manageForm.id = rows.id;
      this.manageForm.username = rows.username;
      this.manageForm.name = rows.name;
      this.manageForm.power = rows.power;
      this.manageForm.statue = rows.statue;
    },
    // 
    manageEditInfoTrue() {
      axios({
        url: './php/update/update_managerInfo.php',
        method: 'post',
        // 
        data: 'id=' + this.manageForm.id +
          '&username=' + this.manageForm.username +
          '&name=' + this.manageForm.name +
          '&power=' + this.manageForm.power +
          '&statue=' + this.manageForm.statue,
        // 
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then((res) => {
        if (res.data === 1) {
          this.$message.warning('');
        } else {
          this.manageGetUserData();
          this.$message.success('');
        }
      }).catch(function (error) { // 
        console.log(error);
      });
    },
    // 
    manageAddUserInfoData() {
      axios({
        url: './php/insert/insert_UserName.php',
        method: 'post',
        // 
        data: 'username=' + this.manageAddUserInfo.username +
          '&pwd=' + this.manageAddUserInfo.pwd +
          '&name=' + this.manageAddUserInfo.name +
          '&power=' + this.manageAddUserInfo.power +
          '&statue=' + this.manageAddUserInfo.statue,
        // 
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }).then((res) => {
        if (res.data === 3) {
          this.$message.error('');
        } else if (res.data === 2) {
          this.$message.success('');
          this.manageGetUserData();
        }
        // 
        this.manageAddUserInfo.username = ''
        this.manageAddUserInfo.pwd = ''
        this.manageAddUserInfo.name = ''
      }).catch(function (error) { // 
        console.log(error);
      });

    },
    // 
    manageSubmitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          // 
          this.manageDialogAddVisible = false;
          this.manageAddUserInfoData();
        } else {
          // 
          return false;
        }
      });
    },
    manageResetForm(formName) {
      this.$refs[formName].resetFields();
    },
    // 
    manageGetUserData() {
      axios
        .get('./php/select/select_UserInfo.php')
        .then((res) => {
          this.manageGetServerData = res.data;
          this.manageTableData = this.manageGetServerData.slice(0, 8);
          this.manageTotalPage = this.manageGetServerData.length
          console.log(this.manageGetServerData)
          console.log(this.manageTableData)
        })
        .catch(function (error) { // 
          console.log(error);
        });
    },
    // 
    manageCurrentChange(nowPage) {
      console.log('' + nowPage)
      // 
      if (this.isFilter) {
        // 
        this.manageFilterFn(nowPage, this.filterTableData)
      } else {
        // 
        this.manageFilterFn(nowPage, this.manageGetServerData)
      }
    },
    // 
    manageFilterFn(page, dataSource) {
      if (page === 1) {
        this.manageTableData = dataSource.slice(0, 8);
      } else {
        let start = (page - 1) * 8
        let end = page * 8
        if (end > dataSource.length) {
          end = dataSource.length;
        }
        this.manageTableData = dataSource.slice(start, end);
      }
    },
  },
  created() {
    this.manageGetUserData();
  }
}
</script>

<style scoped>
.templateWindow {
  height: calc(100% - 40px);
  width: 100%;
  background-color: #D3DCE6;
  position: absolute;
  left: 0;
  top: 40px
}

.windowContent {
  position: absolute;
  left: 10px;
  top: 0;
  width: calc(100% - 10px);
  height: calc(100% - 10px);
}
</style>



