<template>
  <div id="bgId">
    <div class="center-vh" >
      <div class="bg-translucent p-2" @keyup.enter="ajaxPostLog()">
        <div class="flex-box bg-white login-box">
          <div class="login-left p-5">
            <form action="http://localhost/php/log/log.php" method="post" class="login-form">
              <div class="form-group has-feedback">
                <span class="mdi mdi-account" aria-hidden="true"></span>
                <input type="text" class="form-control" id="username" placeholder="" v-model="form.username">
              </div>
              <div class="form-group has-feedback">
                <span class="mdi mdi-lock" aria-hidden="true"></span>
                <input type="password" class="form-control" id="password" placeholder="" v-model="form.pwd">
              </div>
              <div class="form-group has-feedback row">
                <div class="col-7">
                  <span class="mdi mdi-check-all form-control-feedback" aria-hidden="true"></span>
                  <input type="text" v-model="form.captcha" class="form-control" placeholder="">
                </div>
                <div class="col-5 text-right">
                  <img :src="form.captchaSrc" class="pull-right" id="captcha" style="cursor: pointer;"
                       onclick="this.src=this.src+'?d='+Math.random();" title="" alt="captcha">
                </div>
              </div>
              <div class="form-group">
                <button class="btn btn-block btn-primary" type="button" @click="ajaxPostLog()"></button>
              </div>
            </form>
          </div>
          <div class="login-right p-5 d-none d-sm-block">
            <p class="text-white">HTMLBootstrap v4.4.1vue.js + element UI</p>
            <p class="text-white align-self-end">Copyright  2020 <a href="javascript:alert('')"></a>. All
              right
              reserved</p>
          </div>
        </div>
      </div>
    </div>
  </div>

</template>

<script>
import axios from "axios";

export default {
  name: "logHtml",
  data() {
    return {
      bgShow:{
        backgroundImage: "url('/assets/login-bg-3.jpg'))",
        backgroundSize: 'cover'
      },
      form: {
        username: '',
        pwd: '',
        captcha: '',
        captchaSrc:''
      }
    }
  },
  methods: {
    ajaxPostLog() {
      // 
      if (this.form.username.length === 0) {
        this.$message.error('');
      } else if (this.form.pwd.length === 0) {
        this.$message.error('');
      } else if (this.form.captcha.length === 0) {
        this.$message.error('');
      } else if (this.form.username.length < 5 || this.form.username.length > 11) {
        this.$message.error(' 5  11 ');
      } else if (this.form.pwd.length < 6 || this.form.pwd.length > 14) {
        this.$message.error(' 6  14 ');
      } else if (this.form.captcha.length < 4 || this.form.captcha.length > 4) {
        this.$message.error(' 4 ');
        this.form.captchaSrc = this.form.captchaSrc+'?d='+Math.random()
      } else {
        axios({
          url: './php/log/log.php',
          method: 'post',
          // 
          data: 'username=' + this.form.username +
            '&pwd=' + this.form.pwd +
            '&captcha=' + this.form.captcha,
          // 
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        }).then((res) => {
          if (res.data === 1) {
            this.form.captcha = ''
            this.$message.error('');
            this.form.captchaSrc = this.form.captchaSrc+'?d='+Math.random()
          } else if (res.data === 2) {
            this.form.captcha = ''
            this.$message.error('');
            this.form.captchaSrc = this.form.captchaSrc+'?d='+Math.random()
          } else if (res.data === 3) {
            this.$message.success('');
            setTimeout(()=>{
              this.$parent.hideLogHtml();
            },1200)
          } else if (res.data === 4) {
            this.$message.error('');
          }
        }).catch(function (error) { // 
          console.log(error);
        });
      }
    }
  },
  created() {
    this.form.captchaSrc = './php/yzm.php'
  }

}
</script>
<style src="../assets/css/bootstrap.min.css" scoped></style>
<style src="../assets/css/style.min.css" scoped></style>
<style src="../assets/css/materialdesignicons.min.css" scoped></style>
<style scoped>
#bgId{
  height: 100%;
  width: 100%;
  background-image: url('../assets/login-bg-3.jpg');
  background-size: cover;
}

.login-form .has-feedback {
  position: relative;
}

.login-form .has-feedback .form-control {
  padding-left: 36px;
}

.login-form .has-feedback .mdi {
  position: absolute;
  top: 0;
  left: 0;
  right: auto;
  width: 36px;
  height: 36px;
  line-height: 36px;
  z-index: 4;
  color: #dcdcdc;
  display: block;
  text-align: center;
  pointer-events: none;
}

.login-form .has-feedback.row .mdi {
  left: 15px;
}

.login-form .form-group:last-child,
.login-right p:last-child {
  margin-bottom: 0;
}

.login-right {
  background: #67b26f !important;
  background: -moz-linear-gradient(45deg, #67b26f 0, #4ca2cd 100%) !important;
  background: -webkit-linear-gradient(45deg, #67b26f 0, #4ca2cd 100%) !important;
  background: linear-gradient(45deg, #67b26f 0, #4ca2cd 100%) !important;
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#67b26f', endColorstr='#4ca2cd', GradientType=1) !important;
}

.login-box {
  max-width: 700px;
}

.login-right {
  max-width: 50%;
}

.center-vh {
  display: -webkit-box;
  display: flex;
  -webkit-box-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  align-items: center;
  height: 100%;
}

</style>



